.page body {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #181818;
  color: #fbfbfb; }

.page__inner {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.page__content {
  flex-grow: 5;
  position: relative; }

.page__footer-wrapper {
  display: flex;
  flex-grow: 1; }

.page__title {
  font-family: 'Open Sans Condensed', Arial, sans-serif;
  font-weight: 300;
  font-size: 32px;
  line-height: 42px;
  text-transform: uppercase; }
  .page__title--warning {
    color: #d5303a; }

.page__text {
  margin-bottom: 30px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 25px; }

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: absolute;
  z-index: 15;
  top: 0;
  left: 0;
  width: 100%;
  margin-top: 35px;
  padding-left: 30px;
  padding-right: 30px; }
  @media (min-width: 768px) {
    .header {
      width: 100%;
      padding-left: calc(20px + 10px);
      padding-right: calc(20px + 10px); } }
  @media (min-width: 992px) {
    .header {
      padding-left: calc(60px + 10px);
      padding-right: calc(60px + 10px); } }
  .header__link {
    text-decoration: none;
    color: #fbfbfb; }
  .header__title {
    font-family: 'Open Sans Condensed', Arial, sans-serif;
    font-weight: 300;
    font-size: 18px;
    line-height: 25px; }
    .header__title span {
      display: block;
      font-size: 25px;
      text-transform: uppercase; }
  .header__burger {
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content: space-between;
    width: 20px;
    height: 10px;
    cursor: pointer; }
    .header__burger-line {
      width: 100%;
      height: 1px;
      background-color: #bbb;
      box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5); }
    @media (min-width: 768px) {
      .header__burger {
        display: none; } }

.main-nav {
  display: none;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #181818; }
  .main-nav--mobile-active {
    display: flex; }
  @media (min-width: 768px) {
    .main-nav {
      display: inline-flex;
      flex-direction: row;
      justify-content: flex-end;
      position: static;
      background: none;
      width: auto;
      height: auto;
      margin: 0; } }
  .main-nav__close-btn {
    position: relative;
    width: 25px;
    height: 25px;
    margin: 35px 42px 55px auto; }
    .main-nav__close-btn:before, .main-nav__close-btn:after {
      content: ' ';
      position: absolute;
      left: 12.5px;
      width: 2px;
      height: 35px;
      background-color: #d5303a; }
    .main-nav__close-btn:before {
      transform: rotate(45deg); }
    .main-nav__close-btn:after {
      transform: rotate(-45deg); }
    @media (min-width: 768px) {
      .main-nav__close-btn {
        display: none; } }
  .main-nav__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    flex-grow: 5; }
    @media (min-width: 768px) {
      .main-nav__list {
        flex-direction: row;
        flex-grow: 1; } }
  .main-nav__item {
    margin-bottom: 35px;
    margin-left: 30px;
    font-family: 'Open Sans Condensed', Arial, sans-serif;
    font-weight: 300;
    font-size: 32px;
    line-height: 41px;
    color: #fbfbfb;
    text-transform: uppercase; }
    @media (min-width: 768px) {
      .main-nav__item {
        margin-right: 30px;
        margin-bottom: 0;
        font-size: 18px;
        line-height: 25px; } }
  .main-nav__link {
    text-decoration: none;
    color: #fbfbfb; }
    .main-nav__link:visited {
      color: #fbfbfb;
      text-decoration: none; }

.social-links {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: flex-start;
  margin-top: 20px;
  order: 1; }
  @media (min-width: 992px) {
